// up - 
// in - 
import { useDispatch } from "react-redux";
import "../../sass/sign.sass";
import { setCoins } from "../../store/coinSlice";
import { setEarn } from "../../store/earnSlice";
import { setItems } from "../../store/shopSlice";
import { login } from "../../store/userSlice";

const Sign = ({ type }) => {
  const dispatch = useDispatch()
  
  const register = btn => { // 
    const [userName, password, pass2] = btn.target.form
    if(password.value === pass2.value) {
      fetch(`http://localhost:1503/register/${userName.value}/${password.value}`)
    }
  }

  const enter = btn => { // 
    const [userName, password] = btn.target.form

    fetch(`http://localhost:1503/enter/${userName.value}/${password.value}`)
      .then(resp => resp.json())
      .then(data => {
        if(data.message === 'true') {
          dispatch(login(data['user-info'].userName)) //  
          dispatch(setCoins(data['user-info'].coins)) // 
          dispatch(setEarn(data['user-info'].earn)) // 
          data['user-info'].shop.forEach((item, id) => dispatch(setItems({id: id, items: item}))) //   
        }
      })
  }

  return (
    <form id="Sign">
      <a href="#" className="exit">x</a>
      <h2>{type.toUpperCase()}</h2>

      <ul>
        <li>
          <input type="text" placeholder="'" name="username" />
        </li>
        <li>
          <input type="password" placeholder="" name="password" />
        </li>
        {
          type === 'sign up'? //  
            <li>
              <input type="password" placeholder="" name="password" />
            </li> : <></>
        }
        
      </ul>
      
      {
        type === 'sign up'? //  
          <a href="#"><button type="button" onClick={register}></button></a> :
          <a href="#"><button type="button" onClick={enter}></button></a>
      }

      {
        type === 'sign up'? //  
          <a href="#SignIn"></a> :
          <a href="#SignUp"></a>
      }
    </form>
  );
};

export default Sign